.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.input-group input[type="file"]{
  display: none;
}
.input-group label{
  cursor: pointer;
  border: 1px solid;
  padding: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  font-weight: 700;
  padding: 29px;
}

@media screen and (max-width:430px) {
  .main{
    background: rgb(212, 210, 255);
      padding: 25px;
      margin: 10px !important;
      border-radius: 15px;
  } 
}

/* Home setion*/
.input-page{
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 90vh;
}

/* fOR tOOLS SECTION*/

.btn-group input[type="file"]{
  display: none;
}

.btn-group label{
  color: blue;
  padding: 20px;
  border: 1px solid blue;
  border-radius: 0 5px 5px 0;
}
.btn-group label:hover{
  background-color: blue;
  color: #fff;
}

/* Tools secton pdf file*/
.pdf-page{
  display: noen;
  width: 100%;
  overflow: hidden;
}

.create-pdf{
  position: relative;
  padding: 1rem;
  margin: auto;
}

.file-item{
  padding: 0 1 rem;
  background-color: none;
  text-align: center;
  position: relative;
  width: 36rem;
  height: 29rem;
  margin: 1rem;
  display: inline-block;
  transition: 0.3s all ease-in-out;
  box-shadow: rgb(0,0,0,0.24) 0px 3px 8px;
}

.modify button{
  padding: 0.3rem;
  font-size: 1rem;
}

.img-container{
  width: 8.5rem;
  margin: auto;
  height: 32rem;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}

#img{
  position: relative;
  max-width: 25rem;
  max-height: 25rem;
}

#img-name{
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 8.5rem;
  text-align: center;
  margin: 1rem auto;

}

.add-more-file{
  padding: 0 1rem;
  background: none;
  text-align: center;
  position: relative;
  margin: 4.5rem;
  display: inline-block;
  transition: 0.3s all ease-in-out;
  width: 10rem;
  height: 16rem;
  vertical-align: top;
}

.input-cont{
  display: flex;
  flex-direction: column;
  flex-flow: column-reverse;
  border-radius: 50%;
  position: relative;
  text-align: center;
  border: none;
  background:rgb(241, 246,246);
  width: 12rem;
  height: 12rem;
  margin: 2rem -2rem;
}

#image_input{
  opacity: 0;
  display: block;
  width: 9rem;
  height: 9rem;
  border-radius: 50%;
  position: absolute ;
  cursor: pointer;
}

.create-pdf .add-more-file p{
  position: relative;
  top: -3.5rem;
}


/* Responsive for Tools Scetions*/

@media screen and (max-width:1390px) {
  .Tools{
    height: 210vh !important;
  }

  .create-pdf{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
@media screen and (max-width:850px) {
  .Tools{
    height: 225vh !important;
  }

  .create-pdf{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .delete{
    position: relative !important;
    bottom: 0px !important;
    right: 0px !important;
    top: 5px !important;
  }
}

/* Conatct section */

@media screen and (max-width:1500px) {
  .contact{
    display: flex !important;
    height: 90vh !important;
    flex-flow: column wrap !important;
    place-content: stretch space-evenly !important;
    flex-direction: column !important;
    flex-wrap: nowrap !important;
    justify-content: center !important;
    align-items: baseline !important;
  }
}

@media screen and (max-width:700px) {
  .image{
    width: 80%;
  }

}

